import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { useData } from '../components/DataLoader';
import './Research.css';

const ProjectCard = ({ project, isExpanded, onToggle }) => {
  const descriptionRef = useRef(null);
  const [hasOverflow, setHasOverflow] = useState(false);

  useEffect(() => {
    if (descriptionRef.current) {
      const element = descriptionRef.current;
      setHasOverflow(element.scrollHeight > element.clientHeight + 5);
    }
  }, [project.description]);

  const handleCardClick = (e) => {
    if (hasOverflow) {
      onToggle();
    }
  };

  const handleReadMoreClick = (e) => {
    e.stopPropagation();
    onToggle();
  };

  return (
    <Card 
      className={`project-card ${isExpanded ? 'expanded' : ''}`}
      onClick={handleCardClick}
    >
      <div className="card-overlay"></div>
      <Card.Body>
        <Card.Title>{project.titre}</Card.Title>
        <Card.Subtitle className="mb-2 text-muted">{project.sous_titre}</Card.Subtitle>
        <div className="description-container">
          <div 
            ref={descriptionRef}
            className={`description-fade ${!isExpanded && hasOverflow ? 'has-fade' : ''}`}
            style={{ 
              maxHeight: isExpanded ? 'none' : '160px',
              overflow: 'hidden',
              whiteSpace: 'pre-line',
              textAlign: 'justify'
            }}
          >
            <Card.Text>
              {project.description}
            </Card.Text>
          </div>
          {hasOverflow && !isExpanded && (
            <button
              className="read-more-btn"
              onClick={handleReadMoreClick}
            >
              Lire la suite
            </button>
          )}
        </div>
      </Card.Body>
    </Card>
  );
};

function Research() {
  const { data, loading, error } = useData();
  const [expandedProject, setExpandedProject] = useState(null);

  if (loading) return <Container className="my-5"><h2>Chargement...</h2></Container>;
  if (error) return <Container className="my-5"><h2>Erreur de chargement</h2></Container>;
  if (!data.research || data.research.length === 0) return <Container className="my-5"><h2>Aucun projet disponible</h2></Container>;
  


  return (
    <Container className="my-5">
      <h1>Nos Recherches</h1>
      <br />
      <Row>
        {data.research.map((project) => (
          <Col md={6} key={project.id} className="mb-4">
            <ProjectCard
              project={project}
              isExpanded={expandedProject === project.id}
              onToggle={() => setExpandedProject(
                expandedProject === project.id ? null : project.id
              )}
            />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Research;